define(function(require) {

	'use strict';

	//region dependencies
	var Enum = require('Enum'),
		_ = require('underscore');
	//endregion

	return Enum.subClass({
		FORM: {
			type: 'FORM',
			handler: 'formHandler'
		},
		STYLESHEET: {
			type: 'STYLESHEET',
			handler: 'cssHandler'
		},
		BUSINESS_TYPE: {
			type: 'BUSINESS_TYPE',
			handler: 'typeHandler'
		},

		fromType: function(type) {
			var key = _.find(_.keys(this), function(key) {
				return key === type;
			});
			return this[key];
		}
	}, {});

});



