define(function(require) {

	'use strict';

	//region dependencies

	var ControlValueProperty = require('ControlValueProperty');
	var msg = require('ojL10n!rendererMsg/nls/renderer');

	//endregion

	return ControlValueProperty.subClass({}, {
		value: 'VIDEO_SRC',
		label: msg.LABEL_SOURCE_URL,
		init: function() {}
	});
});



