define(function(require) {

	'use strict';

	//region dependencies
	var msg = require('ojL10n!rendererMsg/nls/renderer');
	//endregion

	var Comparators = {
		'IS_TRUE': {
			value: 'IS_TRUE',
			label: msg.IS_TRUE,
			isTrue: function(v1) {
				return !!v1;
			}
		},
		'IS_FALSE': {
			value: 'IS_FALSE',
			label: msg.IS_FALSE,
			isTrue: function(v1) {
				return !Comparators.IS_TRUE.isTrue(v1);
			}
		},
		'EQUALS': {
			value: 'EQUALS',
			label: msg.EQUALS,
			isTrue: function(v1, v2) {
				/* jshint eqeqeq: false */
				//We are intentionally using the == comparator
				//So the user can compare with numbers, as the constant value will always be a string
				return v1 == v2;
			}
		},
		'NOT_EQUALS': {
			value: 'NOT_EQUALS',
			label: msg.NOT_EQUALS,
			isTrue: function(v1, v2) {
				return !Comparators.EQUALS.isTrue(v1, v2);
			}
		},
		'IS_GREATER': {
			value: 'IS_GREATER',
			label: msg.IS_GREATER,
			isTrue: function(v1, v2) {
				return v1 > v2;
			}
		},
		'IS_NOT_GREATER': {
			value: 'IS_NOT_GREATER',
			label: msg.IS_NOT_GREATER,
			isTrue: function(v1, v2) {
				return !Comparators.IS_GREATER.isTrue(v1, v2);
			}
		},
		'IS_LESS': {
			value: 'IS_LESS',
			label: msg.IS_LESS,
			isTrue: function(v1, v2) {
				return v1 < v2;
			}
		},
		'IS_NOT_LESS': {
			value: 'IS_NOT_LESS',
			label: msg.IS_NOT_LESS,
			isTrue: function(v1, v2) {
				return !Comparators.IS_LESS.isTrue(v1, v2);
			}
		},
		'IS_EMPTY': {
			value: 'IS_EMPTY',
			label: msg.IS_EMPTY,
			isTrue: function(v1) {
				return (v1 instanceof Array && v1.length === 0) || (v1 !== 0 && !v1);
			}
		}
	};

	return Comparators;
});



