define(function(require) {

	'use strict';
	// jshint maxstatements: 52

	//region dependencies

	var ControlTypeId = require('ControlTypeId');
	var _ = require('underscore');
	var ActionsMap = require('ActionsMap');
	//endregion

	var basicControlActions = [
		ActionsMap.VALUE, ActionsMap.SHOW,
		ActionsMap.HIDE, ActionsMap.ENABLE, ActionsMap.DISABLE,
		ActionsMap.LABEL, ActionsMap.SET_INVALID, ActionsMap.CLEAR_VALUE
	];
	var inputActions = _.union(basicControlActions, [
		ActionsMap.REQUIRED, ActionsMap.OPTIONAL
	]);
	var inlineInputActions = _.union(inputActions, [
		ActionsMap.HELP, ActionsMap.HINT,
		ActionsMap.READONLY, ActionsMap.EDITABLE,
		ActionsMap.PLACEHOLDER
	]);
	var textActionsWithoutPattern = _.union(inlineInputActions, [
		ActionsMap.MIN_LENGTH, ActionsMap.MAX_LENGTH
	]);
	var textActions = _.union(textActionsWithoutPattern, [
		ActionsMap.PATTERN
	]);
	var valueActions = _.union(inlineInputActions, [
		ActionsMap.MIN_VALUE, ActionsMap.MAX_VALUE
	]);
	var lovActions = _.union(inputActions, [ActionsMap.REFRESH_CONNECTOR, ActionsMap.OPTIONS]);

	var actions = {};
	actions[ControlTypeId.INPUT_TEXT] = textActions;
	actions[ControlTypeId.TEXT_AREA] = textActions;
	actions[ControlTypeId.BUTTON] = [
		ActionsMap.SHOW,
		ActionsMap.HIDE, ActionsMap.ENABLE, ActionsMap.DISABLE,
		ActionsMap.LABEL
	];
	actions[ControlTypeId.SELECT] = lovActions;
	actions[ControlTypeId.CHECKLIST] = lovActions;
	actions[ControlTypeId.CHECKBOX] = inputActions;
	actions[ControlTypeId.RADIO_BUTTON] = lovActions;
	actions[ControlTypeId.NUMBER] = valueActions;
	actions[ControlTypeId.DATE] = valueActions;
	actions[ControlTypeId.TIME] = valueActions;
	actions[ControlTypeId.DATE_TIME] = valueActions;
	actions[ControlTypeId.EMAIL] = textActionsWithoutPattern;
	actions[ControlTypeId.URL] = textActionsWithoutPattern;
	actions[ControlTypeId.MESSAGE] = [
		ActionsMap.SHOW,
		ActionsMap.HIDE, ActionsMap.MESSAGE
	];
	actions[ControlTypeId.LINK] = [
		ActionsMap.DO_NOTHING, ActionsMap.SHOW,
		ActionsMap.HIDE, ActionsMap.LABEL_BINDING, ActionsMap.VALUE
	];
	actions[ControlTypeId.MONEY] = valueActions;
	actions[ControlTypeId.PHONE] = textActionsWithoutPattern;
	actions[ControlTypeId.IMAGE] = [
		ActionsMap.LABEL, ActionsMap.SHOW,
		ActionsMap.HIDE, ActionsMap.IMAGE_URL, ActionsMap.ALTERNATIVE_TEXT
	];
	actions[ControlTypeId.VIDEO] = [
		ActionsMap.LABEL, ActionsMap.SHOW,
		ActionsMap.HIDE, ActionsMap.VIDEO_SRC
	];
	actions[ControlTypeId.PANEL] = [
		ActionsMap.SHOW,
		ActionsMap.HIDE, ActionsMap.LABEL
	];
	actions[ControlTypeId.BUSINESS_TYPE] = [
		ActionsMap.SHOW,
		ActionsMap.HIDE, ActionsMap.LABEL
	];
	actions[ControlTypeId.SECTION] = [
		ActionsMap.SHOW,
		ActionsMap.HIDE, ActionsMap.LABEL,
		ActionsMap.EXPAND, ActionsMap.COLLAPSE
	];
	actions[ControlTypeId.FORM_REFERENCE] = [
		ActionsMap.LABEL, ActionsMap.SHOW, ActionsMap.HIDE, ActionsMap.CHANGE_PRESENTATION
	];
	actions[ControlTypeId.TAB_CONTAINER] = [
		ActionsMap.SHOW,
		ActionsMap.HIDE
	];
	actions[ControlTypeId.TAB] = [
		ActionsMap.SHOW,
		ActionsMap.SELECT,
		ActionsMap.HIDE, ActionsMap.ENABLE, ActionsMap.DISABLE, ActionsMap.LABEL
	];
	var repeatableActions = [
		ActionsMap.REPEATABLE_VALUE,
		ActionsMap.SHOW,
		ActionsMap.HIDE,
		ActionsMap.DISABLE,
		ActionsMap.ENABLE,
		ActionsMap.CAN_ADD_REMOVE_ROWS,
		ActionsMap.CANT_ADD_REMOVE_ROWS,
		ActionsMap.READONLY,
		ActionsMap.EDITABLE,
		ActionsMap.REFRESH_CONNECTOR,
		ActionsMap.ADD_ROW,
		ActionsMap.CLEAR_VALUE
	];
	actions[ControlTypeId.TABLE] = _.union(repeatableActions, [ActionsMap.HIDE_COLUMN, ActionsMap.SHOW_COLUMN]);
	actions[ControlTypeId.REPEATABLE_SECTION] = repeatableActions;
	actions[ControlTypeId.STRING_REPEATABLE_SECTION] = repeatableActions;
	actions[ControlTypeId.NUMBER_REPEATABLE_SECTION] = repeatableActions;
	actions[ControlTypeId.BOOLEAN_REPEATABLE_SECTION] = repeatableActions;
	actions[ControlTypeId.DATE_REPEATABLE_SECTION] = repeatableActions;
	actions[ControlTypeId.TIME_REPEATABLE_SECTION] = repeatableActions;
	actions[ControlTypeId.DATE_TIME_REPEATABLE_SECTION] = repeatableActions;
	actions[ControlTypeId.IDENTITY_BROWSER] = inputActions;

	var repeatedRowActions = [ActionsMap.REMOVE_ROW, ActionsMap.CLEAR_VALUE, ActionsMap.SHOW, ActionsMap.HIDE];
	actions[ControlTypeId.REPEATABLE_SECTION_ROW] = _.union(repeatedRowActions, [ActionsMap.LABEL]);
	actions[ControlTypeId.TABLE_ROW] = repeatedRowActions;

	actions[ControlTypeId.FORM_PRESENTATION] = [ActionsMap.SET_DATA, ActionsMap.CHANGE_PRESENTATION, ActionsMap.TRIGGER_OUTCOME, ActionsMap.PREVENT_SUBMIT, ActionsMap.REFRESH_GLOBAL_CONNECTOR];
	actions[ControlTypeId.ENUM_SELECT] = inputActions;

	return actions;

});



