define(function(require) {

	'use strict';

	//region dependencies

	var ConnectorResolver = require('ConnectorResolver'),
		ko = require('knockout');

	//endregion

	function executeOnTrue(observable) {
		return new Promise((resolve) => {
			const value = ko.unwrap(observable);
			if (value) {
				resolve(value);
			} else {
				const sub = observable.subscribe((newValue) => {
					sub.dispose();
					resolve(newValue);
				});
			}
		});
	}

	return ConnectorResolver.subClass({}, {
		init: function(typeId, context, properties, controlId, filter) {
			this._super(typeId, context, properties, controlId, filter);

			executeOnTrue(context.viewModel.form).then(async (form) => {
				const skipDuringLoad = ko.unwrap(ko.unwrap(this.properties).skipDuringLoad);
				const isStaticForm = context.viewModel.staticForm;
				if (!skipDuringLoad && !isStaticForm) {
					this.loading(true);
					await executeOnTrue(form.loaded);
					this.loadAndSetConnector(form);
				}
			});
		}
	});
});



