define(function(require) {

	'use strict';

	//region dependencies
	var ko = require('knockout');
	//endregion

	return function(params) {
		var self = this;
		self.styleClass = params.styleClass;
		self.icon = ko.utils.unwrapObservable(params.icon);
		self.alt = params.alt || '';
	};
});



