define(function(require) {

	'use strict';

	//region dependencies

	var Class = require('Class'),
		ValueHelper = require('ValueHelper'),
		_ = require('underscore'),
		ko = require('knockout');

	//endregion

	function unwrapValue(value) {
		value = ko.unwrap(value);
		if (value && value.toJS) {
			return value.toJS();
		} else {
			return value;
		}
	}

	return Class.subClass({
		toJS: function(object) {
			var js = _.isArray(object) ? [] : {};
			_.each(object, function(value, key) {
				js[key] = unwrapValue(value);
				if (_.isObject(js[key])) {
					js[key] = this.toJS(js[key]);
				}
			}, this);
			return js;
		},
		//TODO unify this with toJS
		toMinJS: function(object) {
			var js = _.isArray(object) ? [] : {};
			_.each(object, function(value, key) {
				value = unwrapValue(value);
				if (!(_.isObject(value) ? _.isEmpty(value) : ValueHelper.isNullEmptyOrUndefined(value))) {
					js[key] = value;
					if (_.isObject(js[key])) {
						js[key] = this.toMinJS(js[key]);
					}
				}
			}, this);
			return js;
		}
	}, {});
});



