define(function(require) {

	'use strict';

	//region dependencies

	var ControlProperty = require('ControlProperty'),
		msg = require('ojL10n!rendererMsg/nls/renderer');

	//endregion

	return ControlProperty.subClass({}, {
		value: 'ROW_COUNT',
		label: msg.ROW_COUNT,
		init: function() {},
		getValue: function(control) {
			return control.getRows().length;
		}
	});
});



