define(function(require) {

	'use strict';

	//region dependencies

	var msg = require('ojL10n!rendererMsg/nls/renderer');

	//endregion

	var TargetType = {
		PARENT: {
			value: '_parent',
			label: msg.TARGET_TYPE_PARENT
		},
		NEWTAB: {
			value: '_blank',
			label: msg.TARGET_TYPE_NEWTAB
		},
		values: function() {
			return [TargetType.NEWTAB, TargetType.PARENT];
		}
	};
	return TargetType;
});



