define(function(require) {

	'use strict';

	//region dependencies

	var RendererViewModel = require('RendererViewModel'),
		IconComponent = require('IconComponent'),
		rendererCss = require('css/renderer.css'),
		ojMigrationCss = require('css/forms-oj-migration.css'),
		ko = require('knockout');

	var RendererTemplateLoader = require('RendererTemplateLoader');
	var mediaBindingHandler = require('mediaBindingHandler');
	var HandleEventsBinding = require('knockoutExtensions');
	var knockoutExtensions = require('HandleEventsBinding');
	var formKnockoutExtensions = require('formKnockoutExtensions');

	//endregion

	return function() {
		RendererTemplateLoader();
		mediaBindingHandler();
		HandleEventsBinding();
		knockoutExtensions();
		formKnockoutExtensions();

		if (!ko.components.isRegistered('form-renderer')) {
			rendererCss.use();
			ojMigrationCss.use();
			//cannot use just icon due to a bug in ko 3.4 - https://github.com/knockout/knockout/issues/1935
			ko.components.register('actionable-icon', {
				viewModel: IconComponent,
				template: require('iconTemplate')
			});

			ko.components.register('form-renderer', {
				viewModel: RendererViewModel,
				template: require('rendererTemplate')
			});
		}
	};
});



