define(function(require) {

	'use strict';

	//region dependencies
	var DataType = require('DataType'),
		TypeDescription = require('TypeDescription');
	//endregion

	return DataType.subClass({}, {
		init: function(name, items, dataType) {
			var id = this.getTypeName();
			this._super(id, name, 'Enum', '', {
				enum: items
			});
		},
		getTypeDescription: function() {
			return TypeDescription.ENUM;
		},
		isCompatible: function(data) {
			return data.isEnum();
		},
		isEnum: function() {
			return true;
		}
	});

});



