define(function(require) {

	'use strict';

	//region dependencies
	var DataType = require('DataType');
	//endregion

	return DataType.subClass({}, {
		/* jshint maxparams: 6 */
		init: function(name, label, icon, type, format) {
			this._super(this.getTypeName(), name, label, icon, {
				type: type,
				format: format
			});
		}
	});

});



