define(function(require) {

	'use strict';

	//region dependencies

	var Class = require('Class'),
		ControlFactory = require('ControlFactory'),
		_ = require('underscore'),
		UUID = require('UUID');

	//endregion

	return Class.subClass({}, {
		init: function(schemaKey, schema, context, businessControlFactory, parent) {
			var self = this;
			self.id = UUID.createUuid();
			self.name = schemaKey;
			self.parent = parent;
			self.context = context;
			self.schema = schema;
			self.businessControlFactory = businessControlFactory;
			self.data = schema.data || {};
			_.defaults(self.data, {
				properties: {}
			});
			_.defaults(self.data.properties, {
				binding: schemaKey
			});

		},
		create: function() {
			return ControlFactory.createControl(this.id, this.name, this.type, this.data, this.context, this.parent);
		}
	});
});



