define(function(require) {

	'use strict';
	//region dependencies
	var ko = require('knockout'),
		oj = require('ojs/ojcore'),
		templates = {
			iconTemplate: require('iconTemplate'),
			rendererControl: require('rendererControl'),
			rendererMoneyControl: require('rendererMoneyControl'),
			rendererButtonControl: require('rendererButtonControl'),
			rendererChecklistControl: require('rendererChecklistControl'),
			rendererCheckboxControl: require('rendererCheckboxControl'),
			rendererDateControl: require('rendererDateControl'),
			rendererDateTimeControl: require('rendererDateTimeControl'),
			rendererEmailControl: require('rendererEmailControl'),
			rendererLinkControl: require('rendererLinkControl'),
			rendererMessageControl: require('rendererMessageControl'),
			rendererMessageTypeParagraphTemplate: require('rendererMessageTypeParagraphTemplate'),
			rendererMessageTypeHeading1Template: require('rendererMessageTypeHeading1Template'),
			rendererMessageTypeHeading2Template: require('rendererMessageTypeHeading2Template'),
			rendererMessageTypeHeading3Template: require('rendererMessageTypeHeading3Template'),
			rendererMessageTypeHeading4Template: require('rendererMessageTypeHeading4Template'),
			rendererMessageTypeHeading5Template: require('rendererMessageTypeHeading5Template'),
			rendererMessageTypeHeading6Template: require('rendererMessageTypeHeading6Template'),
			rendererNumberControl: require('rendererNumberControl'),
			rendererRadioButtonControl: require('rendererRadioButtonControl'),
			rendererSelectControl: require('rendererSelectControl'),
			rendererSingleSelectTemplate: require('rendererSingleSelectTemplate'),
			rendererMultiSelectTemplate: require('rendererMultiSelectTemplate'),
			rendererTextAreaControl: require('rendererTextAreaControl'),
			rendererTextControl: require('rendererTextControl'),
			rendererTimeControl: require('rendererTimeControl'),
			rendererUrlControl: require('rendererUrlControl'),
			rendererPhoneControl: require('rendererPhoneControl'),
			rendererImageControl: require('rendererImageControl'),
			rendererFormReferenceControl: require('rendererFormReferenceControl'),
			rendererVideoControl: require('rendererVideoControl'),
			rendererIdentityControl: require('rendererIdentityControl'),
			rendererTemplate: require('rendererTemplate'),
			rendererPanelControl: require('rendererPanelControl'),
			rendererSectionControl: require('rendererSectionControl'),
			rendererTableControl: require('rendererTableControl'),
			rendererRepeatableSectionControl: require('rendererRepeatableSectionControl'),
			rendererTabControl: require('rendererTabControl'),
			rendererTableRow: require('rendererTableRow'),
			rendererRepeatableRow: require('rendererRepeatableRow'),
			columnControl: require('columnControl'),
			rendererSectionRow: require('rendererSectionRow'),
			rendererPanelItem: require('rendererPanelItem'),
			rendererRepeatableItem: require('rendererRepeatableItem'),
			rendererTabContainerControl: require('rendererTabContainerControl')
		};

	require('ojs/ojknockout');
	require('ojs/ojcheckboxset');
	require('ojs/ojradioset');
	require('ojs/ojselectcombobox');
	require('ojs/ojinputnumber');
	require('ojs/ojdatetimepicker');
	require('ojs/ojknockout-validation');
	require('ojidentity');

	//endregion
	oj.koStringTemplateEngine.install();

	return function() {

		for (var name in templates) {
			/*istanbul ignore else*/
			if (templates.hasOwnProperty(name)) {
				ko.templates[name] = templates[name];
			}
		}
	};

});



