define(function(require) {

	'use strict';

	//region dependencies
	const StyleTypeId = require('StyleTypeId'),
		msg = require('ojL10n!rendererMsg/nls/renderer'),
		richTextEditorTemplate = require('richTextEditorTemplate'),
		ActionsMap = require('ActionsMap'),
		PropertiesMap = require('PropertiesMap'),
		EventsId = require('EventsId');

	require('koCkEditor')();

	//endregion

	return {
		RICH_TEXT_EDITOR: {
			id: 'RICH_TEXT_EDITOR',
			name: msg.CONTROL_RICH_TEXT_EDITOR,
			icon: 'TEXT_AREA',
			dataType: 'STRING',
			templateName: 'richTextEditorTemplate',
			properties: {
				label: msg.CONTROL_RICH_TEXT_EDITOR,
				defaultValue: '',
				readonly: false,
				help: '',
				hide: false
			},
			styles: [StyleTypeId.LABEL_SIZE],
			translatableProperties: [
				'label', 'defaultValue', 'help'
			],
			events: [
				EventsId.ON_LOAD, EventsId.ON_CHANGE, EventsId.ON_SUBMIT
			],
			eventActions: [
				ActionsMap.VALUE, ActionsMap.SHOW, ActionsMap.HIDE, ActionsMap.READONLY, ActionsMap.EDITABLE, ActionsMap.LABEL, ActionsMap.HELP
			],
			eventProperties: [
				PropertiesMap.VALUE, PropertiesMap.LABEL, PropertiesMap.HIDDEN
			],
			computable: true,
			template: richTextEditorTemplate
		}
	};
});



