define(function(require) {
	'use strict';

	//region dependencies

	var msg = require('ojL10n!rendererMsg/nls/renderer');

	//endregion

	var ScopeType = {
		USER: {
			label: msg.SCOPE_USER,
			value: 'user',
		},
		GROUP: {
			label: msg.SCOPE_GROUP,
			value: 'group'
		},
		ROLE: {
			label: msg.SCOPE_ROLE,
			value: 'role'
		},
		values: function() {
			return [ScopeType.USER, ScopeType.GROUP, ScopeType.ROLE];
		}
	};

	return ScopeType;
});



