define(function(require) {
	/*jshint maxstatements: 49*/

	'use strict';

	//region dependencies

	var ControlTypeId = require('ControlTypeId');
	var _ = require('underscore');
	var PropertiesMap = require('PropertiesMap');
	//endregion

	var commonProperties = [
		PropertiesMap.VALUE, PropertiesMap.HAS_CLASS, PropertiesMap.LABEL, PropertiesMap.HIDDEN, PropertiesMap.DISABLED, PropertiesMap.IS_VALID
	];
	var inputCommonProperties = _.union(commonProperties, [
		PropertiesMap.REQUIRED
	]);
	var textProperties = _.union(inputCommonProperties, [
		PropertiesMap.PLACEHOLDER
	]);
	var valueProperties = _.union(inputCommonProperties, [
		PropertiesMap.MIN_VALUE, PropertiesMap.MAX_VALUE
	]);
	var lovProperties = _.union(inputCommonProperties, [
		PropertiesMap.OPTIONS,
		PropertiesMap.SELECTED_LABEL
	]);

	var properties = {};
	properties[ControlTypeId.INPUT_TEXT] = textProperties;
	properties[ControlTypeId.TEXT_AREA] = textProperties;
	properties[ControlTypeId.BUTTON] = [
		PropertiesMap.HIDDEN, PropertiesMap.LABEL, PropertiesMap.DISABLED
	];
	properties[ControlTypeId.SELECT] = lovProperties;
	properties[ControlTypeId.CHECKLIST] = lovProperties;
	properties[ControlTypeId.CHECKBOX] = inputCommonProperties;
	properties[ControlTypeId.RADIO_BUTTON] = _.union(commonProperties, [
		PropertiesMap.OPTIONS,
		PropertiesMap.SELECTED_LABEL
	]);
	properties[ControlTypeId.NUMBER] = valueProperties;
	properties[ControlTypeId.DATE] = valueProperties;
	properties[ControlTypeId.TIME] = valueProperties;
	properties[ControlTypeId.DATE_TIME] = valueProperties;
	properties[ControlTypeId.EMAIL] = textProperties;
	properties[ControlTypeId.URL] = textProperties;
	properties[ControlTypeId.MESSAGE] = [
		PropertiesMap.HIDDEN, PropertiesMap.MESSAGE
	];
	properties[ControlTypeId.LINK] = [
		PropertiesMap.VALUE, PropertiesMap.LABEL, PropertiesMap.HIDDEN, PropertiesMap.MESSAGE
	];
	properties[ControlTypeId.MONEY] = valueProperties;
	properties[ControlTypeId.PHONE] = textProperties;
	properties[ControlTypeId.IMAGE] = [
		PropertiesMap.HIDDEN, PropertiesMap.LABEL, PropertiesMap.IMAGE_URL
	];
	properties[ControlTypeId.VIDEO] = [
		PropertiesMap.HIDDEN, PropertiesMap.LABEL, PropertiesMap.VIDEO_SRC
	];
	properties[ControlTypeId.PANEL] = [
		PropertiesMap.HIDDEN, PropertiesMap.IS_VALID, PropertiesMap.LABEL
	];
	properties[ControlTypeId.BUSINESS_TYPE] = [
		PropertiesMap.HIDDEN, PropertiesMap.LABEL
	];
	properties[ControlTypeId.SECTION] = [
		PropertiesMap.HIDDEN, PropertiesMap.LABEL, PropertiesMap.IS_VALID, PropertiesMap.EXPANDED
	];
	properties[ControlTypeId.FORM_REFERENCE] = [
		PropertiesMap.HIDDEN, PropertiesMap.LABEL
	];

	properties[ControlTypeId.TAB] = [
		PropertiesMap.HIDDEN, PropertiesMap.LABEL, PropertiesMap.IS_VALID, PropertiesMap.DISABLED, PropertiesMap.SELECTED
	];

	properties[ControlTypeId.TAB_CONTAINER] = [
		PropertiesMap.HIDDEN, PropertiesMap.IS_VALID
	];

	var repeatableProperties = [
		PropertiesMap.VALUE,
		PropertiesMap.HIDDEN,
		PropertiesMap.CAN_ADD_REMOVE_ROWS,
		PropertiesMap.DISABLED,
		PropertiesMap.ROW_COUNT
	];
	properties[ControlTypeId.IDENTITY_BROWSER] = _.union([PropertiesMap.IDENTITY_VALUE], textProperties);
	properties[ControlTypeId.TABLE] = repeatableProperties;
	properties[ControlTypeId.REPEATABLE_SECTION] = repeatableProperties;
	properties[ControlTypeId.STRING_REPEATABLE_SECTION] = repeatableProperties;
	properties[ControlTypeId.NUMBER_REPEATABLE_SECTION] = repeatableProperties;
	properties[ControlTypeId.BOOLEAN_REPEATABLE_SECTION] = repeatableProperties;
	properties[ControlTypeId.DATE_REPEATABLE_SECTION] = repeatableProperties;
	properties[ControlTypeId.TIME_REPEATABLE_SECTION] = repeatableProperties;
	properties[ControlTypeId.DATE_TIME_REPEATABLE_SECTION] = repeatableProperties;

	var repeatedRowProperties = [PropertiesMap.VALUE, PropertiesMap.INDEX];
	properties[ControlTypeId.REPEATABLE_SECTION_ROW] = _.union(repeatedRowProperties, [PropertiesMap.LABEL]);
	properties[ControlTypeId.TABLE_ROW] = repeatedRowProperties;

	properties[ControlTypeId.ENUM_SELECT] = inputCommonProperties;

	properties[ControlTypeId.FORM_PRESENTATION] = [PropertiesMap.IS_VALID, PropertiesMap.OUTCOME];

	return properties;

});



