define(function(require) {
	'use strict';
	var $ = require('jquery');

	return {
		$: $,
		fadeIn: function(condition) {
			var $spinner = this.$('.spinnerContainer');
			if ((arguments.length === 0 || !!condition) && !$spinner.is(':visible')) {
				$spinner.stop().fadeIn();
			}
		},
		fadeOut: function() {
			this.$('.spinnerContainer').stop().fadeOut();
		}
	};
});



