define(function(require) {

	'use strict';

	//region dependencies

	var RepeatableSectionControl = require('RepeatableSectionControl');

	//endregion

	return RepeatableSectionControl.subClass({}, {
		/* jshint maxparams: 7 */
		init: function(id, name, data, context, controlFactory, parent, typeId) {
			var self = this;
			self._super(id, name, data, context, controlFactory, parent, typeId);

		}
	});

});



