define(function(require) {

	'use strict';

	//region dependencies

	var msg = require('ojL10n!rendererMsg/nls/renderer');

	//endregion

	return {
		VERTICAL: {
			value: 'VERTICAL',
			label: msg.LAYOUT_VERTICAL
		},
		HORIZONTAL: {
			value: 'HORIZONTAL',
			label: msg.LAYOUT_HORIZONTAL
		}
	};
});



