define(function(require) {

	'use strict';

	//region dependencies

	var Class = require('Class');

	//endregion

	return Class.subClass({}, {
		value: '',
		label: '',
		accessor: '',
		params: 0,
		init: function(value, label, accessor) {
			this.value = value;
			this.label = label;
			this.accessor = accessor;
		},

		template: function() {
			return false;
		},

		getValue: function(control) {
			return control.properties[this.accessor]();
		}
	});
});



