define(function(require) {

	'use strict';

	//region dependencies
	var Class = require('Class');
	//endregion

	/** @abstract */
	return Class.subClass({}, {
		init: function(reference) {
			this._reference = reference;
		},
		get: function() {
			throw new Error('This function must be overridden');
		}
	});
});



