define(function(require) {

	'use strict';

	//region dependencies

	var CustomControlDefinition = require('CustomControlDefinition'),
		CustomControlList = require('CustomControlList');

	//endregion

	return {
		init: function(controlList) {
			controlList[CustomControlList.RICH_TEXT_EDITOR.id] = new CustomControlDefinition(CustomControlList.RICH_TEXT_EDITOR);
		}
	};

});



