define(function(require) {

	'use strict';

	//region dependencies
	var Class = require('Class'),
		ko = require('knockout');
	//endregion

	return Class.subClass({}, {
		init: function(name, dataType) {
			this.name = ko.observable(name);
			this.dataType = ko.observable(dataType);
		},
		toJS: function() {
			return this.dataType().toJS();
		}
	});

});



