define(function(require) {

	'use strict';

	//region dependencies

	var SetValueAction = require('SetValueAction');

	//endregion

	return SetValueAction.subClass({
		ACTION_NAME: 'IMAGE_URL'
	}, {
		init: function(model, viewModel, scope, control) {
			this._super(model, viewModel, scope, control);
		}
	});
});



