define(function(require) {

	'use strict';

	//region dependencies
	const RenderingQueue = require('RenderingQueue'),
		AsyncRenderer = require('AsyncRenderer');

	//end region

	return {
		init: function(element, valueAccessor, allBindings, viewModel, bindingContext) {
			const templateData = valueAccessor();
			const asyncRenderer = new AsyncRenderer(templateData, element);

			if (templateData.foreach.subscribe) {
				asyncRenderer.subscribeToChanges(element, bindingContext);
			}

			const queue = new RenderingQueue(element, bindingContext, asyncRenderer);

			//start executing the templates.
			queue.execute();

			//descendant bindings are managed by the AsyncTemplateRenderer
			return {
				'controlsDescendantBindings': true
			};
		}
	};
});



