import EventAction from 'EventAction';
import ko from 'knockout';
import _ from 'underscore';

const ChangePresentationAction = EventAction.subClass({
	ACTION_NAME: 'CHANGE_PRESENTATION'
}, {
	init: function(model, viewModel, scope, control) {
		this._super(model, viewModel, scope, control);
		this.presentation = ko.observable(model.presentation || '');
	},
	template: function() {
		return 'presentationSelectTemplate';
	},
	execute: function(control) {
		//This will work for both the FormReferenceControl and the Presentation
		return control.changePresentation(this.presentation()[0]);
	},
	toJS: function() {
		return _.extend({}, this._super(), {
			presentation: this.presentation()
		});
	}
});

export default ChangePresentationAction;



