define(function(require) {

	'use strict';

	//region dependencies

	var ControlTypeId = require('ControlTypeId'),
		BusinessControl = require('BusinessControl');

	//endregion

	return BusinessControl.subClass({}, {
		init: function(schemaKey, schema, context, businessControlFactory, parent) {
			var self = this;
			self._super(schemaKey, schema, context, businessControlFactory, parent);

			self.type = ControlTypeId.CHECKBOX;
		}
	});
});



