define(function(require) {

	'use strict';

	//region dependencies

	var HideColumnEventAction = require('HideColumnAction');

	//endregion

	return HideColumnEventAction.subClass({
		ACTION_NAME: 'SHOW_COLUMN'
	}, {
		init: function(model, viewModel, scope, control) {
			this._super(model, viewModel, scope, control);
			this.hideBoolean = false;
		}
	});
});



