define(function(require) {

	'use strict';

	//region dependencies

	var RestResolver = require('RestResolver');

	//endregion

	return RestResolver.subClass({}, {
		init: function(typeId, context, properties, controlId) {
			this._super(typeId, context, properties, controlId);
		}
	});
});



