define(function(require) {

	'use strict';

	//region dependencies

	var Class = require('Class');

	//endregion

	return Class.subClass({}, {

		init: function(id, controlTemplate) {
			this.id = id;
			this.controlTemplate = controlTemplate;
		},
		properties: function() {
			return {
				controlTemplate: this.controlTemplate
			};
		},
		isBindable: function() {
			return false;
		}
	});
});



