define(function(require) {

	'use strict';

	//region dependencies

	const ControlProperty = require('ControlProperty'),
		EventsId = require('EventsId'),
		msg = require('ojL10n!rendererMsg/nls/renderer');

	//endregion

	return ControlProperty.subClass({}, {
		value: 'OUTCOME',
		label: msg.OUTCOME,
		eventTriggers: [EventsId.ON_SUBMIT.value],
		init: function() {},
		getValue: function(form) {
			return form.outcomeType;
		}
	});
});



