define(function(require) {

	'use strict';

	//region dependencies
	var Class = require('Class');
	//endregion

	/**
	 * Constructs an Item to use in a OjSelect component
	 */
	return Class.subClass({
		create: function(value, label) {
			return {
				value: value,
				label: label
			};
		}
	}, {});
});



