define(function(require) {

	'use strict';

	//region dependencies
	var SelectValueDecorator = require('SelectValueDecorator'),
		_ = require('underscore');

	//endregion

	return SelectValueDecorator.subClass({}, {
		init: function(dependencies) {
			this._super(dependencies);
		},
		_decorate: function(control) {
			this._super(control);

			//@override setValue, but keep reference
			const __superSetValue = control.setValue;
			control.setValue = function(value) {
				if (_.isArray(value)) {
					value = _.map(value, v => v.toString());
				} else {
					value = value.toString();
				}
				__superSetValue.call(control, value);
			};

		}
	});
});



